.RevenueProjections.darkHeader{
  table {
    thead{
      position: sticky;
          top: 0;
          z-index: 2;
  
  }
  tbody{
    
      :has(.lvl\:2){
        td{
        padding-left: 15px !important;
        }
    }

    :has(.lvl\:3){
      td{
        padding-left: 25px !important;
      }
    }

    :has(.lvl\:4){
      td{
        padding-left: 35px !important;
      }
    }

    td{
      &:nth-of-type(-n+2){
        background-color: #D4E7FB !important;
      }
    }

    td{
      &:nth-last-child(-n+2){
        background-color: #f2f29f !important;
      }
    }

    tr{
      &:nth-of-type(-n+2){
        td{
          background-color: $tableSummaryGray !important;
        }
      }
      
    }
  }
  }
}
